@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700;900&display=swap');

body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background: linear-gradient(270deg, #181c2b, #2a2258, #6c63ff, #181c2b);
  background-size: 400% 400%;
  animation: gradientMove 16s ease-in-out infinite;
  color: #f5f6fa;
  min-height: 100vh;
  transition: background 0.5s;
  /* Mobile touch improvements */
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  /* Smooth scrolling for mobile */
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
}

@keyframes gradientMove {
  0% { background-position: 0% 50%; }
  25% { background-position: 50% 100%; }
  50% { background-position: 100% 50%; }
  75% { background-position: 50% 0%; }
  100% { background-position: 0% 50%; }
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  /* Mobile viewport improvements */
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Custom Scrollbar Styling */
::-webkit-scrollbar {
  width: 12px;
  background: #181c2b;
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(120deg, #6c63ff 0%, #2a2258 100%);
  border-radius: 8px;
  box-shadow: 0 2px 8px #6c63ff44;
}
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(120deg, #a7a6f6 0%, #6c63ff 100%);
}
::-webkit-scrollbar-corner {
  background: #181c2b;
}

/* For Firefox */
html {
  scrollbar-width: thin;
  scrollbar-color: #6c63ff #181c2b;
}

/* Mobile-specific improvements */
@media (max-width: 768px) {
  /* Prevent zoom on input focus */
  input, textarea, select {
    font-size: 16px !important;
  }
  
  /* Improve touch targets */
  button, a, [role="button"] {
    min-height: 44px;
    min-width: 44px;
  }
  
  /* Disable hover effects on touch devices */
  @media (hover: none) and (pointer: coarse) {
    *:hover {
      transform: none !important;
    }
  }
}

/* High DPI mobile screens */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}
